<template>
    <div class="table-toolbar">
        <!--  -->
        <div class="table-action-btn">
            <slot></slot>
        </div>

        <div class="tool-table">
            <div class="toolbar-item">
                <i
                    title=""
                    class="ri-refresh-line"
                    @click="refreshTable"
                    style="font-size: 22px; cursor: pointer"
                ></i>
            </div>
            <div class="toolbar-item">
                <a-dropdown :trigger="['click']">
                    <i
                        title=""
                        class="ri-line-height"
                        style="font-size: 22px; cursor: pointer"
                    ></i>
                    <a-menu slot="overlay">
                        <a-menu-item>
                            <a
                                href="javascript:;"
                                @click="adjustSpacing('default')"
                                ></a
                            >
                        </a-menu-item>
                        <a-menu-item>
                            <a
                                href="javascript:;"
                                @click="adjustSpacing('middle')"
                                ></a
                            >
                        </a-menu-item>
                        <a-menu-item>
                            <a
                                href="javascript:;"
                                @click="adjustSpacing('large')"
                                ></a
                            >
                        </a-menu-item>
                        <a-menu-item>
                            <a
                                href="javascript:;"
                                @click="adjustSpacing('small')"
                                ></a
                            >
                        </a-menu-item>
                    </a-menu>
                </a-dropdown>
            </div>
            <div class="toolbar-item">
                <a-popover trigger="click" placement="bottom">
                    <template slot="title">
                        <div
                            style="
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                            "
                        >
                            
                            <span
                                style="cursor: pointer; color: #1890ff"
                                @click="resetFields"
                                ></span
                            >
                        </div>
                    </template>
                    <template slot="content">
                        <div>
                            <div class="column-fields">
                                <ul>
                                    <li
                                        v-for="field in fields"
                                        :key="field.key"
                                    >
                                        <div class="field-item">
                                            <a-checkbox
                                                v-model="field.checked"
                                                :value="field.key"
                                                :checked="
                                                    selectedFields.includes(
                                                        field.key
                                                    )
                                                "
                                                @change="
                                                    changeFields($event, field)
                                                "
                                            ></a-checkbox>
                                            <span class="field-title">{{ field.title }}</span>
                                            <div class="field-actions">
                                                <i 
                                                    class="ri-arrow-up-s-line" 
                                                    @click="moveUp(field)"
                                                    :class="{ 'disabled': isFirstItem(field) }"
                                                    title=""
                                                ></i>
                                                <i 
                                                    class="ri-arrow-down-s-line" 
                                                    @click="moveDown(field)"
                                                    :class="{ 'disabled': isLastItem(field) }"
                                                    title=""
                                                ></i>
                                            </div>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </template>
                    <i
                        title=""
                        class="ri-settings-line"
                        @click="adjustFields"
                        style="font-size: 22px; cursor: pointer"
                    ></i>
                </a-popover>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "TableToolbar",
    props: {
        spacing: {
            type: String,
            default: "default",
        },
        columns: {
            type: Array,
        },
        // 
        virtualColumns: {
            type: Array,
            default: () => {
                return [];
            },
        },
    },
    data() {
        return {
            selectedFields: [],
            fields: [],
        };
    },
    watch: {
        virtualColumns(val) {
            this.fields = val;
            this.fields.map((item) => {
                if (item.checked) {
                    this.selectedFields.push(item.key);
                }
            });
        },
    },
    mounted() {
        this.fields = this.virtualColumns;
    },
    methods: {
        adjustSpacing(Spacing) {
            // 
            // CSS
            this.$emit("update:spacing", Spacing);
        },
        adjustFields() {
            // 
            // Vue
            // this.$emit("update:selectedFields", this.selectedFields);
        },
        refreshTable() {
            // 
            // Vue
            this.$emit("refreshTable");
        },
        moveUp(field) {
            const index = this.fields.findIndex(item => item.key === field.key);
            if (index > 0) {
                // 
                [this.fields[index], this.fields[index - 1]] = [this.fields[index - 1], this.fields[index]];
                this.updateColumns();
            }
        },
        moveDown(field) {
            const index = this.fields.findIndex(item => item.key === field.key);
            if (index < this.fields.length - 1) {
                // 
                [this.fields[index], this.fields[index + 1]] = [this.fields[index + 1], this.fields[index]];
                this.updateColumns();
            }
        },
        isFirstItem(field) {
            return this.fields.indexOf(field) === 0;
        },
        isLastItem(field) {
            return this.fields.indexOf(field) === this.fields.length - 1;
        },
        updateColumns() {
            // fieldscolumns
            const newColumns = this.fields.filter(field => {
                return this.selectedFields.includes(field.key);
            });
            this.$emit("update:columns", newColumns);
        },
        changeFields(e, f) {
            // 
            // 
            if (e.target.checked) {
                this.selectedFields.push(f.key);
            } else {
                this.selectedFields = this.selectedFields.filter((item) => {
                    return item !== f.key;
                });
            }
            // columns
            this.updateColumns();
        },
        resetFields() {
            this.selectedFields = [];
            this.fields.forEach((field) => {
                field.checked = false;
            });
            // columns
            this.updateColumns();
        },
    },
};
</script>

<style scoped>
.table-toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.tool-table {
    /* flex */
    display: flex;
    justify-content: flex-end;
    flex: auto;
}

.toolbar-item {
    display: flex;
    align-items: center;
    margin-left: 10px;
}

.toolbar-item label {
    margin-right: 5px;
}

.field-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.field-title {
    flex: 1;
    margin-left: 8px;
}

.field-actions {
    display: flex;
    margin-left: 8px;
}

.field-actions i {
    cursor: pointer;
    font-size: 18px;
    margin: 0 2px;
    color: #1890ff;
}

.field-actions i.disabled {
    color: #d9d9d9;
    cursor: not-allowed;
}

.column-fields ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.column-fields li {
    padding: 6px 0;
    border-bottom: 1px solid #f0f0f0;
}

.column-fields li:last-child {
    border-bottom: none;
}
</style>
