<template>
    <div ref="animeContainer" class="anime-wrapper">
      <slot></slot>
    </div>
  </template>
  
  <script>
  import anime from 'animejs';
  
  export default {
    name: 'AnimeWrapper',
    props: {
      // anime.js
      config: {
        type: Object,
        required: true
      },
      // 
      autoplay: {
        type: Boolean,
        default: true
      },
      // (ms)
      delay: {
        type: Number,
        default: 0
      },
      // 'mounted', 'visible', 'manual'
      trigger: {
        type: String,
        default: 'mounted',
        validator: (value) => ['mounted', 'visible', 'manual'].includes(value)
      }
    },
    data() {
      return {
        animation: null,
        observer: null,
        isVisible: false
      };
    },
    mounted() {
      console.log(':', this.trigger, ':', this.autoplay);
      
      if (this.trigger === 'mounted' && this.autoplay) {
        console.log('');
        this.$nextTick(() => {
          this.startAnimation();
        });
      } else if (this.trigger === 'visible') {
        console.log('');
        this.setupIntersectionObserver();
      } else {
        console.log('play()');
      }
    },
    methods: {
      setupIntersectionObserver() {
        // 
        this.observer = new IntersectionObserver((entries) => {
          console.log(':', entries[0].isIntersecting);
          if (entries[0].isIntersecting && !this.isVisible) {
            this.isVisible = true;
            this.startAnimation();
            this.observer.disconnect();
          }
        }, { threshold: 0.1 });
        
        if (this.$refs.animeContainer) {
          this.observer.observe(this.$refs.animeContainer);
          console.log('');
        } else {
          console.error('');
        }
      },
      startAnimation() {
        // 
        if (this.animation) {
          this.animation.pause();
        }
        
        // 
        const config = {
          targets: this.$refs.animeContainer.querySelector('div'),
          ...this.config,
          delay: this.delay + (this.config.delay || 0)
        };
        
        // 
        this.animation = anime(config);
        
        // 
        this.$emit('animation-start', this.animation);
        
        // 
        this.animation.finished.then(() => {
          this.$emit('animation-end');
        });
      },
      pause() {
        if (this.animation) {
          this.animation.pause();
          this.$emit('animation-pause');
        }
      },
      play() {
        if (this.animation) {
          this.animation.play();
          this.$emit('animation-play');
        } else {
          this.startAnimation();
        }
      },
      restart() {
        if (this.animation) {
          this.animation.restart();
          this.$emit('animation-restart');
        } else {
          this.startAnimation();
        }
      },
      seek(time) {
        if (this.animation) {
          this.animation.seek(time);
        }
      }
    },
    beforeDestroy() {
      // 
      if (this.animation) {
        this.animation.pause();
        this.animation = null;
      }
      
      if (this.observer) {
        this.observer.disconnect();
        this.observer = null;
      }
    }
  };
  </script>
  
  <style scoped>
  .anime-wrapper {
    display: contents; /* DOM */
  }
  </style>