<template>
  <div>
    <keep-alive :include="caches">
     
        <router-view v-if="refreshTag" style="height: 100%;"></router-view>

    </keep-alive>
  </div>
</template>

<script>
import store from "@/store";
import { mapActions } from "vuex";

export default {
  name: "Content",

  data() {
    return {
     
    };
  },
  computed: {
    refreshTag() {
      return store.state.app.app.refreshTag;
    },
    ...mapActions("cache", ["addCache", "removeCache"]),
    caches() {
      return store.state.cache.caches;
    },
  },

  mounted() {},

  methods: {
    // 
    collectCaches() {
      // 
      this.$route.matched.forEach((routeMatch) => {
        const instance = routeMatch.components?.default;
        const componentName = instance?.name;

        if (process.env.NODE_ENV === "development") {
          this.checkRouteComponentName(componentName, instance?.__file);
        }

        // meta.keepAlive
        if (routeMatch.meta.keepAlive) {
          if (!componentName) {
            console.warn(`${routeMatch.path} name`);
            return;
          }
          store.dispatch("cache/addCache", componentName);
          //   this.addCache(componentName)
        } else {
          store.dispatch("cache/removeCache", componentName);
          //   this.removeCache(componentName)
        }
      });
    },
    // 
    checkRouteComponentName(name, file) {
      if (!this.cmpNames) this.cmpNames = {};
      if (this.cmpNames[name]) {
        if (this.cmpNames[name] !== file) {
          console.warn(
            `${file} ${this.cmpNames[name]}  ${name}`
          );
        }
      } else {
        this.cmpNames[name] = file;
      }
    },
  },
  watch: {
    "$route.path": {
      immediate: true,
      handler() {
        this.collectCaches();
      },
    },
    // $route(to, from) {
    //     this.collectCaches()

    // }
  },
};
</script>

<style lang="scss" scoped></style>
