/**
 * 
 * @param {*} extraHeight ( Number,74) 
 * @param {*} id tabletableid
 */
export function getTableScroll({ extraHeight, id },customHeight = 74) {
    if (typeof extraHeight == "undefined") {
      //  64 + 10
      extraHeight = customHeight
    }
    let tHeader = null
    if (id) {
      tHeader = document.getElementById(id) ? document.getElementById(id).getElementsByClassName("ant-table-thead")[0] : null
    } else {
      tHeader = document.getElementsByClassName("ant-table-thead")[0]
    }
    //
    let tHeaderBottom = 0
    if (tHeader) {
      tHeaderBottom = tHeader.getBoundingClientRect().bottom
    }
    //--
    // let height = document.body.clientHeight - tHeaderBottom - extraHeight
    let height = `calc(100vh - ${tHeaderBottom + extraHeight}px)`
    return height
  }
  