import router from "@/router";
// import screenfull from "screenfull";
import fullscreen from "../fullscreen";
import AppConst from "../appConst";

//  goToPage
const goToPage = (url) => {
    // 
    router.push(url);
};

// 
const fullScreen = () => {
    // screenfull.toggle();
    fullscreen.toggle();
};

const settingColor = (color) => {
    if (color == "default") {
        color = AppConst.defaultColor;
    }
    const el = document.documentElement;
    window.less
        .modifyVars({
            "@primary-color": color,
            "@link-color": color,
            "@btn-primary-bg": color,
        })
        .then(() => {
            console.log("");
            // 
            localStorage.setItem("color", color);
        })
        .catch((error) => {
            alert("");
            console.log(error);
        });

    //  --pc --mobile
    el.style.setProperty("--PC", color);
};

export default {
    goToPage,
    fullScreen,
    settingColor,
};