import { defineComponent } from "vue";
import AppConst from "../appConst";

const GRdoPageEditBase = defineComponent({
  props: {
    /** rdo, */
    rdoEditParams: { type: Object },
    /** savesave */
    saveTag: { type: String },
    /** refresh */
    refreshTag: { type: String },
    /** reset */
    resetTag: { type: String },
  },
  data() {
    return {
      _ndoName: "",
      rdoId: "",
      rdoBaseId: "",
      operationName: undefined,
      operationTime: undefined,
      loading: false,
      isRevOfRcdId: false,
      rdo: {}


    };
  },
  mounted() {
    this.rdo = this.rdoEditParams;
  },
  watch: {
    saveTag(newVal) {
      if (newVal) {
        this.save();
      }
    },
    refreshTag(newVal) {
      if (newVal) {
        this.resetPageForms();
        this.refresh();
      }
    },
    resetTag(newVal) {
      if (newVal) {
        this.reset();
      }
    },
    rdo(newVal) {
      console.log('rdoEditParms', newVal);
      if (newVal) {
        this.rdoEditParamsChangeSelf(newVal);
      }
    }
  },
  computed: {
    
  },
  methods: {
    rdoEditParamsChangeSelf(input) {
      // 
      this.reset();

      // 
      if (!input) {
        return;
      }

      // 
      this.rdoId = input.rdoId;
      this.rdoBaseId = input.rdoBaseId;
      this.isRevOfRcdId = input.isRevOfRcdId;

      // 
      this.init();
    },
    /**  */
    resetPageForms() {
      if (this.$refs.form) {
        try {
          this.$refs.form.clearValidate();
        } catch (e) {}
      }
    },
    /**  */
    refresh(showMsg = true) {
      // 
      this.resetFormData();
      // 
      this.resetPageForms();
      // 
      this.init();

      if (showMsg) {
        this.refreshSuccess();
      }
    },
    /**  */
    refreshSuccess(name, version) {
      if (!name) {
        this.$message.success(AppConst.message.refreshSuccess);
        return;
      }
      if (this.rdoEditParams) {
        this.$message.success(
          `${AppConst.message.refreshSuccess}-${name}(${version})-${this.$moment().format(
            "YYYY/MM/DD HH:mm:ss"
          )}`
        );
        
      }
    },
    /**  */
    saveSuccess(name) {
      this._ndoName = name;

      // 
      let msg = AppConst.message.createSuccess;
      if (this.rdoId) {
        msg = AppConst.message.updateSuccess;
      }
      if (this.rdoEditParams) {
        this.$message.success(
          `${msg}-${name}-${this.$moment().format("YYYY/MM/DD HH:mm:ss")}`
        );
      }
    //   this.refresh(false);
    },
    /**  */
    init() {},
    /**   */
    resetFormData() {},
    /**   */
    save() {},
    /**  */
    reset() {
      // 
      this.rdoId = null;
      this.rdoBaseId = undefined;
      this.isRevOfRcdId = false;
      this.operationName = undefined;
      this.operationTime = undefined;
      // 
      this.resetFormData();
      // 
      this.resetPageForms();
    },
    /**
     * 
     * @param result id
     */
    handleSaveEndBackView(result) {
      this.$emit('rowClick', {
        id: result.baseId,
        rdoBaseId: result.baseId,
        rdoId: result.id,
        name: result.name,
        version: result.version,
        isRevOfRcdId: result.isRevOfRcdId
      });
    },
    /**  */
    refreshParent(params) {
      if (!params) {
        params = {};
      }
      if (params.reset) {
        this.reset();
      }
      this.$emit("childRefresh", params);
    },
    checkPageForms() {
      return new Promise((resolve, reject) => {
        this.$refs.form
          .validate()
          .then(() => {
            resolve(true);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    // 
    async checkError() {
      try {
        let valid = await this.checkPageForms();
        return valid;
      } catch (error) {
        return;
      }
    },
  },
});

export default GRdoPageEditBase;
