import { defineComponent } from "vue";
import AppConst from "../appConst";

const GNdoPageEditBase = defineComponent({
  props: {
    /** ndo, id/ndoBaseId/ndoId/ */
    ndoEditParams: { type: Object },
    /** savesave */
    saveTag: { type: String },
    /** refresh */
    refreshTag: { type: String },
    /** reset */
    resetTag: { type: String },
  },
  data() {
    return {
      _ndoName: "",
      id: "",
      loading: false,

    };
  },
  mounted() {
    this.id = this.ndoEditParams?.id;
    this.init();
  },
  watch: {
    saveTag(newVal) {
      if (newVal) {
        this.save();
      }
    },
    refreshTag(newVal) {
      if (newVal) {
        this.resetPageForms();
        this.refresh();
      }
    },
    resetTag(newVal) {
      if (newVal) {
        this.reset();
      }
    },
  },
  computed: {
    
  },
  methods: {
    /**  */
    resetPageForms() {
      if (this.$refs.form) {
        try {
          this.$refs.form.clearValidate();
        } catch (e) {}
      }
    },
    /**  */
    refresh(showMsg = true) {
      // 
      this.resetFormData();
      // 
      this.resetPageForms();
      // 
      this.init();

      if (showMsg) {
        this.refreshSuccess();
      }
    },
    /**  */
    refreshSuccess(name, version) {
      if (!name) {
        this.$message.success(AppConst.message.refreshSuccess);
        return;
      }
      if (this.ndoEditParams) {
        this.$message.success(
          `${AppConst.message.refreshSuccess}-${name}(${version})-${this.$moment().format(
            "YYYY/MM/DD HH:mm:ss"
          )}`
        );
        
      }
    },
    /**  */
    saveSuccess(name) {
      this._ndoName = name;

      // 
      let msg = AppConst.message.createSuccess;
      if (this.id) {
        msg = AppConst.message.updateSuccess;
      }
      if (this.ndoEditParams) {
        this.$message.success(
          `${msg}-${name}-${this.$moment().format("YYYY/MM/DD HH:mm:ss")}`
        );
      }
    //   this.refresh(false);
    },
    /**  */
    init() {},
    /**   */
    resetFormData() {},
    /**   */
    save() {},
    /**  */
    reset() {
      // 
      this.id = null;
      this.operationName = undefined;
      this.operationTime = undefined;
      // 
      this.resetFormData();
      // 
      this.resetPageForms();
    },
    /**
     * 
     * @param result id
     */
    handleSaveEndBackView(id) {
      this.$emit("rowClick", {
        id: id,
        name: this._ndoName,
      });
    },
    /**  */
    refreshParent(params) {
      if (!params) {
        params = {};
      }
      if (params.reset) {
        this.reset();
      }
      this.$emit("childRefresh", params);
    },
    checkPageForms() {
      return new Promise((resolve, reject) => {
        this.$refs.form
          .validate()
          .then(() => {
            resolve(true);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    // 
    async checkError() {
      try {
        let valid = await this.checkPageForms();
        return valid;
      } catch (error) {
        return;
      }
    },
  },
});

export default GNdoPageEditBase;
