
    

    var oBtn;
    var oSearchInput;
    var oSearchContainer;
  
    /**
     * @description  active 
     */
    const handleBtnClick = () => {
      oSearchContainer.classList.add('active')
      oSearchInput.focus()
    }
  
    /**
     * @description  active 
     */
    const handleBlur = () => {
      oSearchContainer.classList.remove('active')
    }
  
    const bindEvent = () => {
      oBtn.addEventListener('click', handleBtnClick)
      oSearchInput.addEventListener('blur', handleBlur)
    }
  
    const init = () => {
        /** @type HTMLButtonElement */
        oBtn = document.querySelector('.btn')
        /** @type HTMLInputElement */
        oSearchInput = document.querySelector('.search-input')
        oSearchContainer = document.querySelector('.search-container')
      bindEvent()
    }
  
    
    export default init;
